html,
body {
  height: 100%;
  width: 100% !important;
  background: #ffffff !important;
  font-family: "Calibri";
  padding-right: 0 !important;
  overflow: auto !important;
}

h3 {
  color: #000000 !important;
}
h2 {
  padding: 0px;
  /*border-bottom: thin solid rgb(39, 37, 37);*/
  text-decoration: underline;
  font-weight: 550;
  font-size: 21px;
  margin-bottom: 17px;
  margin-top: 10px;
  margin-left: 10px;
}
h1 {
  padding: 0px;
  font-size: 24px;
  margin-bottom: 17px;
  margin-top: 10px;
  font-weight: 550;
  color: white;
}
h4 {
  color: white;
  text-align: center;
  font-size: 18px;
}

.header-border {
  width: 100%;
  margin: 0px 0px 50px 0px;
  background: #e8e8e8 !important;
  /*border: rgb(13 56 5 / 80%) solid 2px; */
  border-bottom: grey solid 2px;
  border-bottom: grey solid 2px;
  border-right: grey solid 2px;
  border-left: grey solid 2px;
}
.heading,
.heading-subtitle {
  text-align: center;
}
/*    
   .heading > span, .heading-subtitle > span {
      background-color: #f4f5eb;
      box-shadow: #f4f5eb 11px 0px 0px,
      #f4f5eb -13px 0px 0px;
    } */

span > .heading,
.heading-subtitle > span {
  background: #337ab7;
  padding: 10px;
}
span > .heading1 {
  background: #337ab7;
  padding: 10px;
  margin: 25px 0px 5px 0px;
}
ul {
  margin-top: -25px;
  padding: 0;
  list-style-type: none;
}
.btn-link {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  color: #000000;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
}
a {
  color: #000000;
}
a:visited {
  color: #000000;
} /* Visited link    */
a:hover {
  color: #0380ee;
} /* Mouse over link */
